#weight-class{
    .weight-class-container{
        -webkit-box-sizing : border-box;
        -moz-box-sizing    : border-box;
        box-sizing         : border-box;
        min-width          : 703px;
        min-height         : 703px;
        background         : url(../img/weight-class.jpg) no-repeat 50% 50%;
        background-size    : auto;
        margin             : 0 auto;
        padding            : 50px 90px;
    }

    h3{
        font   : normal 32px/120% @navFont;
        color  : #ff0000;
        margin : 0 0 20px;
    }

    img{
        margin: 0 0 20px;
    }
}
