@import "bootstrap/mixins";

#rules{

    .rules-container{
        max-width  : 720px;
        min-height : 400px;
        margin     : 0 auto;
        background : #202020 url(../img/rules-bg.jpg) no-repeat top right;

        &.visible-xs{background: #202020;}
    }

    ul, li{
        list-style : none;
        margin     : 0;
        padding    : 0;
    }

    nav{
        display : block;
        width   : 100%;
        clear   : both;
        float   : left;

        ul, li{
            float    : left;
            position : relative;
            overflow : hidden;
        }

        ul{width: 100%}


        li{
            display: block;
            .skew(-20deg, 0);

            &:first-child{
                margin-left: -20px
            }

            &.cycle-pager-active, &:hover{
                a{
                    background   : #ff0000;
                    border-right : 1px solid #642828;
                    .transition(~"background 0.5s, border 0.25s");
                }
            }
        }

        a{
            background      : #3a3a3a;
            border-right    : 1px solid #343434;
            font            : 300 20px/100% @navFont;
            color           : #fff;
            padding         : 0 50px;
            letter-spacing  : 1px;
            position        : relative;
            text-decoration : none;
            display         : table-cell;
            vertical-align  : middle;
            height          : 70px;
            .transition(~"background 0.5s, border 0.25s");
        }

        span{
            display: block;
            .skew(20deg, 0);
        }
    }

    .slide{
        display : block;
        width   : 100%;
        clear   : both;
        height: 330px;
    }
}
