// general defenition
body {
  margin     : 0;
  padding    : 0;
  background : #000;
  font-family: @contentFont;

  &.page-not-found {
    color: #2a2a2a;
  }
}

#container {
  // width     : 810px;
  max-width : 810px;
  margin    : 0 auto;
  // position  : relative;
}

#main{
    position: relative;
    z-index: 10;
}

section{
  margin: 40px 0;

  &#about{margin-top: 0}
}

// back to top style
.back-top{
    position        : fixed;
    right           : -60px;
    bottom          : 65px;
    font            : 400 14px/150% @contentFont;
    color           : #fff;
    background      : #3a3a3a;
    padding         : 15px 25px;
    text-align      : center;
    text-decoration : none;
    z-index         : 200;
    opacity         : 0;
    .transition(~"all 0.5s");

    &.active{
        opacity : 1;
        right   : 0px;
        .transition(~"all 0.5s");
    }

    &:hover{
        color           : #fff;
        text-decoration : none;
        background      : #ff0000;
        .transition(~"all 0.5s");
    }
}

// acordion styles
.acordion{
    display : block;
    width   : 100%;
    padding : 10px;

    ul, li{
        display    : block;
        clear      : both;
        list-style : none;
        padding    : 0;
        margin     : 0;
    }

    h3{
        height        : 75px;
        padding       : 25px 30px;
        font          : 400 20px/100% @navFont;
        color         : #fff;
        background    : #ff0000;
        display       : block;
        border-bottom : 1px solid #343434;
        margin        : 0;
    }

    a{
        height          : 75px;
        padding         : 25px 30px;
        font            : 400 20px/100% @navFont;
        color           : #fff;
        background      : #3a3a3a;
        display         : block;
        border-bottom   : 1px solid #343434;
        text-decoration : none;
        .transition(~"all 0.5s");

        &:hover{
            background : #ff0000;
            .box-shadow(#343434 0px -3px 0px 0px inset);
            .transition(~"all 0.5s");
        }
    }

    article{
        padding  : 0px;
        display  : block;
        height   : 0;
        overflow : hidden;
        .transition(~"all 0.5s");
    }

    h4, p, img{
        display    : block;
        margin     : 0 auto 10px;
        text-align : left;
        padding    : 0;
        padding    : 5px 10px;
    }

    h4{
        font: 400 32px/150% @navFont;
        color: #ff0000;
    }

    li.active{
        a{
            background : #ff0000;
            border     : none;
            .box-shadow(#343434 0px -3px 0px 0px inset);
            .transition(~"all 0.5s");
        }

        article{
            .transition(~"all 0.5s");
            height     : 420px;
        }
    }


    // spesific acordion
    &.acordion-rules{
        article{
            text-align: center;

            img{margin-top: 20px;}
        }

    }
}

