// unknown style
.navbar-brand > img {
  margin-top: -20px;
}
.form-signin,
.error-message {
  max-width: 330px;
  margin: 0 auto;
}
.form-signin .form-signin-heading,
.form-signin .checkbox {
  margin-bottom: 10px;
}
.form-signin .checkbox {
  font-weight: normal;
}
.form-signin .form-control {
  position: relative;
  height: auto;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input {
  margin-bottom: 7px;
}

.nomargin,
.paging-nomargin {
    ul {
        margin: 0;
    }
}
.actions {
    a {
        margin: 0 10px 0 0;
    }
    input[type=submit].btn {
        padding: 0 !important;
    }
}

.sosmed {
    li {
        margin-top: 20px !important;
        margin-right: 17px;
        margin-left: 0;

        &:last-child {
            margin-right: 15px;
        }
    }
    a {
        width: 33px;
        height: 33px;
        display: block;
        text-indent: -9999px;
        overflow: hidden;
        background: url(../img/btn-social.png) no-repeat 0 0 !important;

        &.fb {
            background-position: 0 0 !important;
        }
        &.tw {
            background-position: -35px 0 !important;
        }
        &.instagram {
            background-position: -71px 0 !important;
        }
        &.profile {
            background-position: -108px 0 !important;
        }
    }
}

.back-button {
    font               : 400 14px/150% 'Droid Sans', sans-serif, 'helvetica neue', helvetica;
    color              : #fff;
    background         : #3a3a3a;
    padding            : 15px 25px;
    text-align         : center;
    text-decoration    : none;
    -webkit-transition : all 0.5s;
    transition         : all 0.5s;
    position           : relative;
    z-index            : 10;

    &:hover {
        color              : #fff;
        text-decoration    : none;
        background         : #ff0000;
        -webkit-transition : all 0.5s;
        transition         : all 0.5s;
    }
}

.slimScrollBar {
    cursor: pointer;
}
