#disciplines{
    .disciplines-container{
        -webkit-box-sizing : border-box;
        -moz-box-sizing    : border-box;
        box-sizing         : border-box;
        min-width          : 703px;
        min-height         : 703px;
        background         : url(../img/weight-class.jpg) no-repeat 50% 50%;
        background-size    : auto;
        margin             : 0 auto;
        padding            : 50px 90px;
    }

    .visible-xs{
        ul, li{
            list-style : none;
            display    : inline-block;
            text-align : center;
        }

        li{margin: 0 5px}
    }
}
