.cage-fact-container{

    ul, li{
        list-style : none;
        margin     : 0;
        padding    : 0;
    }

    li{padding: 15px 0}

    h4{
        color  : #ff0000;
        font   : 700 20px/120% @navFont;
        margin : 0;
    }

    p{
