#leaderboard {
    padding: 0 14px;
    margin-top: 30px;
    margin-bottom: 20px;

    h2 {
        font           : 400 32px/100% @navFont;
        color          : #ed2124;
        display        : block;
        text-align     : center;
        margin         : 0 0 20px;
        letter-spacing : 2px;
    }

    .tabs {
        position: relative;
        width: 100%;
        overflow: hidden;
        margin-bottom: 0;
        font-weight: 300;

        nav {
            text-align: center;
            height: 70px;

            a {
                font-family: 'american_captainregular';
                color: #fff;
                display: block;
                font-size: 24px;
                line-height: 2.5;
                padding: 7px 1.25em 3px;
                white-space: nowrap;
                letter-spacing: 2px;
                text-decoration: none;
            }
        }

        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            display: inline-block;

            li {
                margin: 0;
                background: #3a3a3a;
                width: 391px;
                display: block;
                float: left;
                position: relative;

                &.tab-current {
                    background: #ec2426;
                    z-index: 100;
                }
            }
        }

        .content {
            background: url(../img/bg-leaderboard.png) no-repeat center 165px;

            section {
                font-size: 24px;
                padding:0;
                display: none;
                margin: 0 auto;
                font-family: 'american_captainregular';
                letter-spacing: 1px;

                &.content-current {
                    display: block;
                }

                &#users-leaderboard {
                    .row-content-fighter {
                        font-size: 30px;
                    }
                }

                .loader-bar {
                    padding: 30px 0;
                    background: url(../img/loader-bar.gif) no-repeat center center;
                }

                .head-title {
                    color: #fff;
                    border: 2px solid #3a3a3a;
                    border-top: 0;
                    padding: 22px 0 18px 0;

                    span {
                        display: inline-block;
                    }
                    .rangking {
                        padding: 0 0 0 23px;
                        width: 100px;
                    }

                    .fighter-name {
                        width: 175px;
                        text-align: center;
                    }
                    .category {
                        padding: 0 0 0 25px;
                        width: 180px;
                    }

                    .supporter {
                        width: 150px;
                        text-align: center;
                    }

                    .score {
                        width: 150px;
                        text-align: center;
                    }

                    // User Leaderboard

                    .player-name{
                        width: 120px;
                        text-align: center;
                    }

                    .badges {
                        width: 135px;
                        text-align: center;
                    }

                    .points {
                        width: 70px;
                        text-align: center;
                    }

                    .my-fighter {
                        text-align: center;
                        padding: 0 0 0 20px;
                        width: 310px;
                    }
                }


                .row-content-fighter {
                    color: #ed1f24;
                    padding: 15px 0;
                    border: 2px solid #3a3a3a;
                    border-top: 0;

                    .tooltip {
                        .opacity(100);
                        position: fixed;

                        &.bottom {
                            .tooltip-arrow {
                                border-bottom-color: #ed2024;
                            }
                        }
                        .tooltip-inner {
                            background: #ed2024;
                            border-radius: 0;
                            font-size: 20px;
                        }
                    }

                    span {
                        display: inline-block;
                        vertical-align: middle;
                    }

                    .rangking {
                        text-align: center;
                        padding: 0 0 0 23px;
                        width: 100px;
                    }
                    .fighter-name {
                        width: 175px;
                        text-align: left;

                        .fighter {
                            display: block;
                            margin: 0 auto;
                            position: relative;
                            width: 68px;
                            height: 67px;
                        }

                        img {
                            margin: 0 auto 4px;
                            height: 67px;
                            z-index: 5;
                            left: 0;
                            top: 0;
                            position: relative;
                            display: block;
                        }
                        .frame {
                            z-index: 10;
                            left: 0;
                            top: 0;
                            position: absolute;
                            width: 68px;
                            height: 67px;
                            display: block;
                            background: url(../img/frames.png) no-repeat 0 0;
                        }

                    }
                    .category {
                        width: 180px;
                        padding: 0 0 0 25px;
                    }

                    .supporter {
                        width: 150px;
                        text-align: center;
                    }

                    .score {
                        width: 150px;
                        text-align: center;
                    }

                    // User Leader board
                    .my-fighter {
                        padding: 0 0 0 20px;
                        width: 310px;
                        text-align: center;

                        .fighter {
                            display: inline-block;
                            position: relative;
                            width: 68px;
                            height: 67px;
                        }

                        img {
                            margin: 0 auto 4px;
                            height: 67px;
                            z-index: 5;
                            left: 0;
                            top: 0;
                            display: block;
                            position: relative;
                        }
                        .frame {
                            z-index: 10;
                            left: 0;
                            top: 0;
                            position: absolute;
                            width: 68px;
                            height: 67px;
                            display: block;
                            background: url(../img/frames.png) no-repeat 0 0;
                        }
                    }

                    .badges {
                        width: 135px;
                        text-align: center;

                        img {
                            width: 50px;
                            height: 50px;
                            margin-bottom: 4px;
                        }
                    }

                    .points {
                        width: 70px;
                        text-align: center;
                    }

                    .player-name {
                        width: 120px;
                        img {
                            display: block;
                            margin: 0 auto;
                            width: 80px;
                            height: 80px;
                            border-radius: 50%;
                        }
                    }
                }
            }
        }
    }

    /* Fallback example */
    .no-js .content section {
        display: block;
        padding-bottom: 2em;
        border-bottom: 1px solid #47a3da;
    }
}
