#whats-mimma{
    .whats-mimma-container{
        -webkit-box-sizing : border-box;
        -moz-box-sizing    : border-box;
        box-sizing         : border-box;
        min-width          : 703px;
        min-height         : 703px;
        background         : url(../img/whats-mma.jpg) no-repeat 50% 50%;
        background-size    : auto;
        margin             : 0 auto;
        padding            : 50px 90px;
    }

    h3{
        font   : normal 32px/120% @navFont;
        color  : #ff0000;
        margin : 0 0 60px;
    }

    img{
        margin: 0 0 30px;
        border: 2px solid #3a3a3a;
    }

    p{
        font       : normal 14px/150% @contentFont;
        color      : #fff;
        padding    : 0 50px;
        margin-top : 390px;
    }
}
