#fighter{
    &.fighter-category,
    &.fighter-class{
        padding: 0 14px;

        .item {
            width: 246px;
            float: left;
            margin-right: 19px;

            &:nth-child(3n) {
                margin-right: 0;
            }
        }

        h2 {
            font           : 400 40px/150% @navFont;
            color          : #ed2124;
            display        : block;
            text-align     : center;
            margin         : 0 0 20px;
            letter-spacing : 2px;
        }

        .frame{
            text-align    : center;
            cursor        : pointer;
            position      : relative;
            margin-bottom : 20px;
            border: 2px solid #3a3a3a;
            .transition(border 0.2s ease-in-out);

            &:hover {
                border-color: #ed2025;
            }

            a, a:hover {
                text-decoration: none;

                &.detail-fighter {
                    span {
                        img {
                            height: 27px;
                        }
                    }
                }
            }

            h4, span{
                font           : 200 20px/100% @navFont;
                color          : #fff;
                display        : block;
                clear          : both;
                background     : #ee1f25;
                margin         : 0;
                padding        : 10px 0;
                letter-spacing : 2px;
                position: relative;
                z-index: 5;
                white-space: nowrap;
                width: 100%;
                overflow: hidden;              /* "overflow" value must be different from "visible" */
                text-overflow: ellipsis;
            }

            figure {
                display: block;
                width: 100%;
                position: relative;
                z-index: 2;

                img{
                    max-width: 100%
                }
            }
        }
    }
}

// popup style
#popup{
    border  : 5px solid #3a3a3a;
    background: #000;
    float   : left;
    width   : 705px;
    margin  : 0;
    padding : 35px 20px;

    .detail-left {
        width: 337px;

        figure {
            height: 467px;
            border: 2px solid #3a3a3a;
            position: relative;

            .picture {
                display: block;
                margin: 0 auto;
                height: 100%;
            }

            .flag {
                position: absolute;
                top: 12px;
                right: 15px;
                z-index: 1000;
            }
        }

        .statistic {
            color: #fff;
            padding: 21px 0 11px 0;
            text-align: center;
            width: 112px;
            background: #ed2024;
            display: block;
            float: left;
            font-family: "american_captainregular";
            font-size: 36px;
            line-height: 14px;
            letter-spacing: 1px;

            small {
                font-family: "Droid Sans";
                font-size: 10px;
                letter-spacing: normal;
            }

            span {
                display: block;
            }

            &.score{
                width: 113px;
                border: 2px solid #8b191b;
                border-width: 0 2px;
            }
        }

        .btn-pick {
            border: 0;
            outline: 0;
            clear: both;
            font-size: 20px;
            color: #fff;
            line-height: 30px;
            text-decoration: none;
            background: #ed2024;
            display: block;
            width: 292px;
            margin: 20px auto 0;
            text-align: center;
            padding: 14px 0;

            &.clicked {
                background: #ed2024 url(../img/loading.gif) no-repeat 65px center;
            }
        }
    }
    .detail-right {
        width: 295px;
        color: #fff;
        font-family: "Droid Sans";

        h3 {
            margin-top: 0;
            font-family: "american_captainregular";
            font-size: 32px;
            line-height: 26px;
            letter-spacing: 3px;
            color: #ed1f24;
        }
        em {
            letter-spacing: 1px;
        }

        .information {
            padding: 15px 0 0 0;
            list-style: none;

            li {
                clear: both;
            }

            label {
                font-weight: normal;
                font-size: 14px;
                display: block;
                float: left;
                width: 125px;
                letter-spacing: 1px;
            }
            span {
                display: block;
                float: left;
                width: 170px;
                position: relative;
                color: #ed2024;
                letter-spacing: 1px;

                &:before {
                    display: inline-block;
                    color: #fff;
                    position: absolute;
                    left: -15px;
                    top: 0;
                    content: ": ";
                }
            }
        }

        .btn {
            display: block;
            margin: 10px 0 5px 0;
            font-size: 20px;
            color: #fff;
            text-align: left;
            font-weight: 700;
            border-radius: 0;

            i {
                font-size: 35px;
                line-height: 40px;
                font-weight: 700;
            }

            span {
                vertical-align: top;
                padding-top: 7px;
                padding-left: 25px;
                display: inline-block;
            }

            &-share-fighter {
                background: #395a98;
            }

            &-tweet-fighter {
                background: #29a6df;
            }
        }
    }

    #social-post{
        width      : 100%;
        display    : block;
        margin-top : 20px;

        ul, li{
            margin  : 0;
            padding : 0;
            display : block;
            float   : left;
        }

        ul{
            width      : 100%;
        }

        li{
            padding       : 10px;
            background    : #fff;
            border-bottom : 1px solid #e9e9ec;
            width         : 100%;
            // .box-shadow(#e9e9ec 0px 1px 0px 1px);

            &:last-child{
                margin-bottom : 0;
                border-bottom : none;
            }
        }

        figure, div{
            float: left;
        }

        figure{
            width: 50px;
            height: 50px;

            img{
                width: 50px;
            }
        }

        div{
            width       : 75%;
            margin-left : 5%;
            position    : relative;
        }

        i{
            position    : absolute;
            top         : 0;
            right       : 0;
            color       : #fff;
            font-size   : 12px;
            font-weight : 100;

            &.icon-facebook{
                background: #395a98;
            }

            &.icon-twitter {
                background : #29a6df;
            }
        }

        span{
            display : block;
            clear   : both;
        }

        .name{color: #3a589c}
        .text{color: #212020}
        .posttime{color: #8f95a5}
    }
}
#shareForHim,
#shareForHimTwitter {
    .share-post-container {
        width: 650px !important;

        .group-by-hastag {
            padding: 10px;
            margin-bottom: 10px;
            // border: 1px solid #ccc;

            h4 {
                font-family    : "american_captainregular";
                color: #ed1f24;
                text-align: center;
                margin: 0 0 10px 0;
                font-size      : 22px;
                line-height    : 24px;
                letter-spacing : 3px;
            }
        }
    }
}

#shareForHim, .popup-container {
    border     : 5px solid #3a3a3a;
    background : #000;
    float      : left;
    width      : 705px;
    margin     : 0;
    padding    : 35px 15px 20px;

    h3 {
        margin-top     : 0;
        text-align     : center;
        font-family    : "american_captainregular";
        font-size      : 32px;
        line-height    : 26px;
        letter-spacing : 3px;
        color          : #ed1f24;
        margin-bottom  : 15px;
    }

    .share-text {
        color     : #686564;
        font-size : 18px;
        padding   : 15px;
        margin    : 0 0 10px 0;
        cursor    : pointer;
        border    : 2px solid #3a3a3a;

        &:hover {
            border-color: #ed2024;
        }

        &.selected {
            background: #ed2024;
            border-color: #ed2024;
            color: #fff;
        }

        strong {
            display: block;
            margin: 0 0 10px 0;
        }
    }

    .btn-share-now {
        outline: 0;
        border-radius: 0;
        padding: 10px 0;
        text-align: center;
        width: 115px;
        display: block;
        margin: 20px auto 0;
        clear: both;

        &:active,
        &:disabled {
            background: lighten(#ed2024, 10%)
        }
    }

    .slimScrollDiv, .support{
        display : block;
        clear   : both;
        width   : 100%;
        float   : left;
    }
}


#my-figher-list {
    clear: both;
}
