#mma-growth{
    .mma-growth-container{
        max-width  : 720px;
        min-height : 500px;
        margin     : 0 auto;
        background : #202020 url(../img/mma-growth-bg.jpg) no-repeat top right;

        &.visible-xs{background: #202020;}
    }

    nav{
        float: left;

        ul,li{
            padding    : 0;
            margin     : 0;
            list-style : none;
        }

        .title{
            margin: 0;
            border-right: 1px solid #343434;
            border-bottom: 1px solid #343434;
        }

        .title, a{
            width           : 120px;
            text-align      : center;
            font            : 200 24px/70px @navFont;
            background      : #3a3a3a;
            display         : block;
            height          : 100%;
            text-decoration : none;
            border-right    : 1px solid #343434;
            border-bottom   : 1px solid #343434;
            color           : #fff;
            letter-spacing  : 1px;
        }

        li{
            &.cycle-pager-active, &:hover{
                a{
                    background   : #ff0000;
                    border-right : 1px solid #642828;
                    .transition(~"background 0.5s, border 0.25s");
                }
            }
        }
    }

    .slider-mma{
        float     : left;
        max-width : 80%;

        h2{
            color          : #fff;
            font           : 200 32px/100% @navFont;
            margin         : 35px 30px;
            color          : #ff0000;
            text-align     : left;
            letter-spacing : 1px;
        }

        ul, li{
            margin  : 0 !important;
            padding : 0 !important;
            width   : 580px !important;
            display : block;
            clear   : both;
        }

        li{
            width   : 580px;
            height  : 420px;
            display : block;
            clear   : both;
        }

        h4,p{
            text-align     : left;
            margin         : 0px;
            margin-left    : 30px;
            color          : #fff;
            letter-spacing : 1px;

        }

        h4{
            font   : 200 20px/120% @navFont;
            height : 40px;
        }

        p{
            font   : 200 14px/150% @contentFont;
            height : 80px;
        }
    }
}
