@media (min-width:320px) and (max-width:767px) {

    h2{
        display : block;
        float   : left;
        width   : 100%;
    }

    // top back button
    .back-button{
        width: 100%;
        margin-top: 15px;
        margin-bottom: 20px;
    }

    #container{
        display  : block;
        width    : auto;
        overflow : hidden;
    }

    img{
        max-width: 100%;
    }

    #whats-mimma, #rules, #weight-class, #bracket, #mimm-tournamet, #mma-growth, #cage-fact{
        display : block;
        width   : 100%;
        float   : left;
    }

    #whats-mimma{
        background-image: url(../img/mobile/whats-mma.jpg);

        p{
            font-size: 13px;
        }
    }

    #disciplines .disciplines-container{
        background-size: cover;
    }

    #profile{
        margin  : 0;
        padding : 0;

        .slider-profile, .slider-profile li{
            width: 100%;
        }

        .slideshow{
            width: 100%;

            article{display: none}
        }

        #profile-block{
            width: 100%;

            .card-info{position: relative;}

            figure{margin: 0 auto}

            p{margin-top: 0}
        }

        img{margin-top: 0}
    }

    #fighter{
        .list-items .item, .list-category .item{
            margin  : 0 auto !important;
            float   : none !important;
            display : block;
        }
    }


    // popup fighter show
    #popup{
        width     : 300px !important;
        max-width : 100%;

        .detail-left, .detail-right{
            width         : 100%;
            display       : block;
            clear         : both;
            margin-bottom : 20px;
        }

        .detail-left figure, figure img.picture{
            width     : 100%;
            max-width : 100%;
            height    : auto;
        }

        .detail-left .statistic{
            width: 33.33% !important
        }

        .detail-right .information span, .detail-right .information label{
            width: 50%
        }

         .detail-right .btn span {
            padding-left: 0;
         }
    }

    #how-to-play{
        width     : 320px;
        max-width : 100%;

        img{
            max-width: 200px;
        }

        p{padding: 0 10px}
    }

    #leaderboard{
        .tabs nav {height: auto}
        .tabs nav a{padding: 0 10px}
        .tabs nav span{float: left;}

        .tabs .content section .head-title span{
            width      : 18% !important;
            padding    : 0 !important;
            font-size  : 10px;
            text-align : center;
        }
    }

    #figtherList span, #playerList span{
        width      : 18% !important;
        padding    : 0 !important;
        font-size  : 10px;
        text-align : center;

        &.fighter{
            width: 100% !important;

            img{
                width      : 70%;
                height     : auto !important;
                margin-top : 5px !important;
            }
        }

        &.frame{
            width: 100% !important;
            background-size: contain !important;
            background-position: 50% 50% !important;
        }

        &.player-name img{
            width: 100% !important;
            height: auto !important;
        }
    }

    section{
        display             : block;
        margin              : 0 auto;
        background-position : 50% 50%;
        background-size     : auto;
        background-repeat   : no-repeat;
        padding             : 40px 60px;
        -webkit-box-sizing  : border-box;
        -moz-box-sizing     : border-box;
        box-sizing          : border-box;
        float               : left;
        width               : 100%;

        >div{
            min-width: 0px !important;
        }

        h3{
            margin-top     : 10px;
            letter-spacing : 1px;
            text-transform : uppercase;
            font           : 700 32px/120% @navFont;
            color          : #ff0000;
        }

        img{margin-top: 20px;}

        p{
            margin-top : 20px;
            font       : normal 14px/150% @contentFont;
            color      : #fff;
            padding    : 0 50px;
        }
    }

    .video-wrapper{
        position       : relative;
        padding-bottom : 56.25%; /* 16:9 */
        padding-top    : 25px;
        height         : 0;

        iframe {
            position : absolute;
            top      : 0;
            left     : 0;
            width    : 100%;
            height   : 100%;
        }
    }

    // navigation
    .top-nav{

        .mobile-menu{
            position        : absolute;
            padding-right   : 1.25em;
            font-size       : 14px;
            text-decoration : none;
            right           : 20px;


            &:after {
                content    : "";
                position   : absolute;
                right      : 0;
                top        : 1.5em;
                width      : 1em;
                height     : 0.15em;
                background : #fff;
                box-shadow :0 0.25em 0 0 #fff,0 0.5em 0 0 #fff;
                .transition(~"all 0.5s");
            }

            &:hover, &.active{
                color : #565656;

                &:after{
                    .transition(~"all 0.5s");
                    background : #565656;
                    box-shadow : 0 0.25em 0 0 #565656,0 0.5em 0 0 #565656;
                }
            }
        }

        ul{
            margin-top : 73px;
            background : #000;
            max-height : 0;
            overflow   : hidden;
            opacity    : 0;
            .transition(~"all 0.5s");

            &.active{
                max-height: 420px;
                opacity: 1;
                .transition(~"all 0.5s");
            }
        }

        li{
            display    : block;
            text-align : left;
            background : #000;
            padding    : 0 10px;

            &.active a, &:hover a{
                color           : #fff !important;
                text-decoration : underline;
            }

            &.fighter-menu{
                padding : 0 10px;
                margin  : 0;
            }

            a{
                display: block;
                line-height : 50px;
            }
        }

        .sub-menu {

            ul{
                display    : none;
                max-height : 0;
                margin-top : 0;
            }

            &.active{
                ul{
                    display    : block;
                    max-height : 400px;
                    opacity    : 1;
                }
            }

            li.active a, li a:hover{
                color: #ff0000 !important;
            }

            a{line-height: 40px}
        }
    }


    /* rules section */
    .rules-container{
        #pager li, #pager span{
            .skew(0deg, 0);
            line-height : 75px;
            display     : block;
            width       : 100%;
        }

        #pager li:first-child{
            margin-left : 0;
        }

        #pager a{
            display : block;
            width   : 100%;
        }

        .slider .slide{
            width: 100%;
            height: 100%;

            li{
                width   : 100%;
                height  : 100%;
                display : block;
            }
        }
    }

    footer{
        nav ul.right , nav ul.left{
            float      : none;
            display    : block;
            text-align : center;
            margin     : 0;
            padding    : 0;
            width      : 100%;
        }

        nav li{
            float   : none;
            display : inline-block;
        }
    }
}

