@import "bootstrap/mixins";

#profile{
    margin-top: 0;

    figure{
        width: 100%;
        img{
            max-width: 100%
        }
    }

    .slideshow {
        width: 650px;
    }

    #profile-block {
        width: 130px;
        margin-right: 5px;
        height: 355px;
        position: relative;

        .card-info {
            position: absolute;
            bottom: 0;
            right: 0;
        }

        figure{
            width: 130px;
            height: 130px;
            margin-top: 2px;
            img{
                max-width: 100%
            }
        }

        .fullname {
            line-height: 14px
        }
    }
}

.slider-profile{
    margin  : 0;
    padding : 0;

    li{
        position : relative;
        margin   : 0;
        padding  : 0;
        width    : 100%;
        border: 1px solid #202020;
        border-width: 0 1px 1px 0;
        .border-right-radius(0px 0px 2px 0);
    }

    img{
        max-width : 100%;
        width     : 100%;
    }

    article{
        width      : 70%;
        background : rgba(237, 32, 36, 0.7);
        position   : absolute;
        bottom     : 0;
        left       : 0;
        padding    : 10px;
    }

    p, b{
        color         : #fff;
        font          : 400 12px/150% @contentFont;
        margin-bottom : 10px;
    }

}
#cycle-pager{
    position   : absolute;
    bottom     : -10px;
    right      : 0;
    z-index    : 100;
    text-align : right;
    padding    : 0 15px;

    span{
        font-family : arial;
        font-size   : 35px;
        width       : 10px;
        height      : 35px;
        display     : inline-block;
        color       : rgba(255, 255, 255, 0.3);
        cursor      : pointer;
        margin      : 0 3px;

        &.cycle-pager-active{color : rgba(255, 255, 255, 1);}
    }
}

.my-profile{

    span{
        font    : 400 18px/150% @navFont;
        color   : #fff;
        display : block;
        clear   : both;
        letter-spacing: 1px;

        &.red{color: #ff0000}
    }

    p{
        font          : 400 25px/100% @navFont;
        color         : #fff;
        display       : block;
        clear         : both;
        background    : #ee1f25;
        margin-bottom : 0;
        border-top    : 2px solid #8b171a;
        padding       : 7px 0;
        letter-spacing: 2px;
        span{font: 200 10px/150% @contentFont;}

        label {
            margin-bottom: 0;
        }
    }
}

.badges-content{
    margin-top: 40px;

    .loader-bar {
        padding: 30px 0;
        background: url(../img/loader-bar.gif) no-repeat center center;
    }

    .btn {
        border-radius: 0 !important;
        font-size: 14px;
        text-transform: uppercase;
        padding-top: 10px;
        padding-bottom: 10px;

        &.btn-claim {
            padding: 7px 15px;
        }
    }

    h2{
        font           : 400 40px/150% @navFont;
        color          : #ed2124;
        display        : block;
        text-align     : center;
        margin-bottom  : 20px;
        letter-spacing : 2px;
    }

    .badges{
        padding       : 0 10px;
        width         : 100%;
        margin-bottom : 20px;
        height        : 290px;
        text-align    : center;
        overflow      : hidden;
        .transition(height 0.5s ease-in);

        .badge-item {
            width      : 27%;
            display    : inline-table;
            text-align : center;
            padding    : 20px 0 10px;
            position   : relative;
            margin     : 0 10px 10px 0;
            height     : 278px;
            min-height : 285px;
            .box-shadow(inset 0px 0px 0px 1px #3a3a3a);

            &:hover{
                .box-shadow(inset 0px 0px 0px 2px #ed2025);

                .info {
                    display: block !important
                }
            }

            &:nth-child(4n) {
                clear: both;
            }
        }

        .icon-earned{
            position : absolute;
            top      : 0;
            left     : 0;
            width    : 74px;
            height   : 74px;
            background: url(../img/earned.png) no-repeat 0 0;
        }

        .icon-dot {
            font-size: 15px;
        }

        figure, span{
            display       : block;
            clear         : both;
            margin-bottom : 10px;
        }

        span{
            color   : #fefefe;
            font    : 600 13px/120% @contentFont;
            padding : 0 10px;

            &.success{
                color: #ff0000;
            }

            &.info {
                color: #ff0000;
                display: none;
                .transition(display 0.5s ease-in-out);
            }
        }
    }
}

#badge-popup, #pointReward, #fighter-message-thanks{
    background: #000 url(../img/background-badge.jpg) no-repeat center center;

    h3{line-height: 150%}

    h2, p, figure{
        margin-bottom: 20px;
    }

    p{
        font: 400 16px/150% @contentFont;
        color: #fff;
    }

    a{
        clear           : both;
        font-size       : 20px;
        color           : #fff;
        line-height     : 30px;
        text-decoration : none;
        background      : #ed2024;
        display         : block;
        border-radius   : 0;
        text-transform  : uppercase;
        width           : 150px;
        margin          : 30px auto 0;
        text-align      : center;
        padding         : 10px 0;
    }
}

#personal-detail{

    .input-block{padding: 0 10px}

    p{margin-bottom: 20px}

    p, label{
        font    : 300 16px/120% @contentFont;
        color   : #fff;
        display : block;
    }

    label{
        position: relative;

        &.error{
            position  : absolute;
            top       : 35px;
            right     : 5px;
            color     : #ff0000;
            font-size : 10px;
        }

        &.terms .error{
            right : auto;
            left  : 0;
        }
    }

    .input-text{
        display : block;
        clear   : both;
        height  : 40px;
        color   : #000;
        padding : 5px 10px;
        margin  : 5px 0 25px;
        width   : 100%;
    }

    .submit{
        display : block;
        clear   : both;
        margin  : 20px 0;


        input[type="submit"]{
            clear           : both;
            font-size       : 20px;
            color           : #fff;
            line-height     : 30px;
            text-decoration : none;
            background      : #ed2024;
            display         : block;
            border-radius   : 0;
            text-transform  : uppercase;
            width           : 150px;
            margin          : 30px auto 0;
            text-align      : center;
            padding         : 10px 0;
            border          : none;
        }
    }
}


#how-to-play {
    border     : 5px solid #3a3a3a;
    background : #000;
    float      : left;
    width      : 705px;
    margin     : 0 0 20px 0;
    padding    : 30px 15px;

    h3 {
        margin-top: 0;
        margin-bottom: 30px;
        text-align: center;
        font-family: "american_captainregular";
        font-size: 32px;
        line-height: 26px;
        letter-spacing: 3px;
        color: #ed1f24;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        color: #fff;
    }
    li {
        font-size: 18px;

        p {
            font-weight: 100;
            margin-bottom: 30px;

            &:first-child {
                font-weight: 700;
                margin-bottom: 10px;
            }
        }
    }

    .btn.choose-fighter {
        clear: both;
        font-size: 20px;
        color: #fff;
        line-height: 30px;
        text-decoration: none;
        background: #ed2024;
        display: block;
        border-radius: 0;
        text-transform: uppercase;
        width: 280px;
        margin: 30px auto 0;
        text-align: center;
        padding: 14px 0;
    }
}


#fighter-eliminated {
    width: 620px;
    height: 633px;
    background: #000 url(../img/eagle.png) no-repeat right 5px;
    border: 5px solid #3a3a3a;

    #fighter-photo {
        width: 247px;
        height: 347px;
        margin: 30px auto 0;
        border: 2px solid #3a3a3a;
        overflow: hidden;


        .name {
            display: block;
            background: #ed2024;
            font-family: "american_captainregular";
            font-weight: 100;
            font-size: 25px;
            letter-spacing: 2px;
            color: #fff;
            line-height: 35px;
            white-space: nowrap;
            padding: 3px 5px;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        figure {
            display: block;
            width: 100%;
            height: 347px;
            overflow: hidden;
            position: relative;
            z-index: 10;
            background: #000;

            img {
                max-height: 100%;
                max-width: 100%;
            }

            h3 {
                position: absolute;
                z-index: 15;
                top: 0;
                background: rgba(0, 0, 0, 0.6);
                padding: 141px 0 135px 0;
                margin: 0;
                text-align: center;
                width: 100%;
            }
        }
    }

    h3 {
        margin-top: 0;
        margin-bottom: 30px;
        text-align: center;
        font-family: "american_captainregular";
        font-size: 32px;
        line-height: 26px;
        letter-spacing: 3px;
        margin: 30px 0 0 0;
        color: #ed1f24;
    }

    p {
        text-align: center;
        font-size: 22px;
        color: #f1f1f1;
        padding: 20px 0 15px 0;
    }

    .btn-choose {
        border: 0;
        outline: 0;
        clear: both;
        font-size: 20px;
        color: #fff;
        line-height: 30px;
        text-decoration: none;
        background: #ed2024;
        display: block;
        width: 292px;
        margin: 0 auto;
        text-align: center;
        padding: 14px 0;
        text-transform: uppercase;

        &.clicked {
            background: #ed2024 url(../img/loading.gif) no-repeat 65px center;
        }
    }
}
