/* responsive style for media phone */

/* Landscape phones and down */
@media (max-width: 480px) {

    #container{
        display  : block;
        width    : auto;
        overflow : hidden;
    }

    img{
        max-width: 100%;
    }

    .weight-class-container, .whats-mimma-container, .disciplines-container, .bracket-container{
        padding: 50px 30px !important;

        p{padding: 0 10px !important}
    }

}
