/* top nav */
.logo{
    margin         : 0;
    min-height     : 317px;
    text-align     : center;
    display        : block;
    vertical-align : middle;
    padding-top    : 25px;
    background: url(../img/top-header.jpg) no-repeat center center;
}

header{
    min-height : 390px;
    display    : block;
    position   : relative;
    z-index    : 200;

    nav{
        top        : -101px;
        float      : left;
        width      : 100%;
        max-width  : 810px;
        height     : 50px;
        background: #ed2024;
        // background : url(../img/top-nav.jpg) repeat-x;
        .transition(~"top 0.5s");

        &.fixed{
            position : fixed;
            top      : 0;
            .transition(~"top 0.5s");
        }

        ul{
            float      : none;
            width      : 100%;
            margin     : 0;
            padding    : 0;
            text-align : center;
            width      : auto;
        }

        li{
            display : inline-block;
            padding  : 0 20px;

            &.fighter-menu {
                margin  : 0 0 0 20px;
            }

            &.active, &:hover{
                a {
                    color: #fff;
                    -webkit-background-clip : text;
                    -moz-background-clip    : text;
                    background-clip         : text;
                    text-decoration         : none;
                }
            }

            &.parent{
                position: relative;

                &:hover {
                    background: #000;

                    .sub-nav{
                        display : block;
                        opacity : 1;
                    }
                }

                .sub-nav{
                    position  : absolute;
                    display   : block;
                    min-width : 140px;
                    display   : none;
                    opacity   : 0;
                    left: 0;
                    top: 50px;

                    li{
                        display       : block;
                        width         : 100%;
                        float         : none;
                        margin        : 0;
                        background    : #100e0e;
                        text-align    : left;
                        padding       : 0 15px;
                        // border-bottom : 3px solid #343434;

                        a {
                            display: block;
                        }

                        &:last-child{border: none}

                        &.active, &:hover {
                            background: #ed2024;
                        }
                    }

                    a {
                        line-height : 40px;
                        color       : #fff;
                    }
                }
            }
        }

        a {
            font                    : normal 12px/50px @contentFont;
            color                   : #fff;
            background-color        : transparent;
            // text-shadow             : 0 0 0 rgba(0,0,0,0.5);
            -webkit-background-clip : text;
            -moz-background-clip    : text;
            background-clip         : text;
            text-decoration         : none;
            .transition(~"all 0.5s");
        }

        .pull-right li{margin: 0 5px}

        .icon-header{
            width      : 33px;
            height     : 33px;
            background : url(../img/sprites.png) no-repeat 0px -37px;
            display    : inline-block;
            .hide-text();

            &.twitter{background-position: -50px -37px;}
            &.instagram{background-position: -100px -37px;}
            &.profile{background-position: -150px -37px;}
        }
    }
}


/* bottom nav */

footer{

    nav{
        background : #2a2a2a;
        width      : 100%;
        float      : left;

        ul{
            margin     : 0;
            padding    : 0;
            text-align : center;

            &.left{
                float       : left;
                margin-left : 12px;
            }
            &.right{
                float        : right;
                margin-top   : 16px;
                margin-right : 12px;
            }
        }

        li{
            display     : inline-block;
            margin      : 0 8px;
        }

        a{
            font  : 200 10px/60px @contentFont;
            color : #fff;

            &:hover{
                text-decoration: none;
            }
        }

        .social{
            height     : 30px;
            width      : 32px;
            float      : left;
            display    : block;
            background : url(../img/sprites.png) no-repeat 0 0;

            &.twitter{
                background-position: -40px 0px;
            }

            &.instagram{
                background-position: -85px 0;
            }

            &.youtube{
                background-position: -128px 0;
            }
        }
    }
}
