.video-news{

    .video-wrapper {
        // width: 520px;
    }

    .video-container {
        position       : relative;
        padding-bottom : 50%;
        padding-top    : 30px;
        height         : 0;
        overflow       : hidden;
    }

    .video-container iframe,
    .video-container object,
    .video-container embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .schedule{
        float      : left;
        padding    : 0 0 15px;
        // width      : 290px;
        height     : 325px;
    }

    ul{
        margin  : 0;
        padding : 0;
    }

    li{
        list-style : none;
        padding : 12px 15px;
        background : #202020;
        border : 3px solid #ff0000;
        border-width: 0 3px 3px 3px;

        &:first-child{
            padding : 10px 15px;
        }
    }

    h3{
        font    : 700 22px/100% @navFont;
        color   : #fff;
        margin  : 0;
        padding : 0;
    }

    h4, span, a{
        display       : block;
        clear         : both;
        margin-bottom : 6px;
    }

    a{margin-bottom: 0}

    h4{
        color  : #ff0000;
        font   : normal 32px/120% @navFont;
        margin : 0;
    }

    span{
        color : #fff;
        font  : normal 12px/120% @contentFont;
    }

    a{
        color           : #fff;
        font            : 700 12px/120% @contentFont;
        text-decoration : underline;

        &:hover{
            color : #ff0000;
            .transition(~"all 0.5s");
        }


    }
}
